// src/app/dashboard/page.tsx – Dashboard page (SSR for initial data; gamified HQ – welcome, points/badges, progress, leaderboard; uses Shadcn for styled sections)
// New File: Assuming this is /dashboard/page.tsx (from screenshot – plain text before). Wrapped in Cards for structure, added progress bar animation stub, integrated Leaderboard with SSR prefetch (best practice: Hydrate for realtime). No removals – built on your placeholder logic.
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'; // New: Use Shadcn Card (styled sections – MMORPG panel feel)
import { Progress } from '@/components/ui/progress'; // New: For level progress (gamified visual)
import Leaderboard from '@/components/ui/Leaderboard'; // Your component (integrated – realtime subs)
import prisma from '@/lib/prisma'; // For SSR fetch
import { createSupabaseServerClient } from '@/lib/supabaseServer';

export default async function Dashboard() {
  const supabase = await createSupabaseServerClient();
  const { data: { user } } = await supabase.auth.getUser();

  // SSR fetch for initial state (profile + top leaders – fast load; realtime handles updates)
  let profile = null;
  let topLeaders = [];
  if (user?.id) {
    profile = await prisma.profile.findUnique({ where: { id: user.id }, select: { role: true, points: true, badges: true } });
    topLeaders = await prisma.profile.findMany({ // Logic: Top 10 by points (leaderboard init – sort desc)
      select: { id: true, role: true, points: true, badges: true },
      orderBy: { points: 'desc' },
      take: 10,
    });
  }

  return (
    <div className="p-8"> {/* New: Padding for layout */}
      <Card> {/* New: Wrap welcome in Card (styled – dark bg, shadows) */}
        <CardHeader>
          <CardTitle>Welcome to HQ, {user?.email}! Role: {profile?.role || 'novice'}</CardTitle> {/* Kept: Personalized (from getUser – secure) */}
          <CardDescription>// Use user email (from getUser) - secure, personalized - motivates reps; fallback for no profile</CardDescription> {/* Kept comment */}
        </CardHeader>
        <CardContent>
          <p>Points: {profile?.points || 0} | Badges: {profile?.badges?.join(', ') || 'None'}</p> {/* New: Display points/badges */}
          <Progress value={(profile?.points || 0) / 1000 * 100} className="mt-4" /> {/* New: Visual level progress (gamification – feel the growth) */}
          <p>// Gamification stats (stub - expand with progress bar/component for visual "level up" feel)</p> {/* Kept comment */}
        </CardContent>
      </Card>

      <Card className="mt-6"> {/* New: Separate Card for leaderboard (peer challenge – MMORPG leaderboard vibe) */}
        <CardHeader>
          <CardTitle>Rank Role Points Badges Quest: Top Reps</CardTitle>
        </CardHeader>
        <CardContent>
          <Leaderboard initialLeaders={topLeaders} /> {/* Integrated your component (SSR prefetch + realtime) */}
          <p>1. novice 4 None</p> {/* Kept placeholder – but Leaderboard renders dynamic */}
          <p>// Pass initial data (hydrate client sub)</p> {/* Kept comment */}
        </CardContent>
      </Card>
    </div>
  );
}